import React, { Component } from "react";
import ReactDOM from "react-dom";
import {
  ThemeProvider,
  createTheme,
  SoundsProvider,
  createSounds
} from "arwes";
import soundSettings from "./variables/soundSettings.jsx";
import { LoaderGeneral } from "./components/LoaderComponents";

// import { Provider } from "react-redux";
// import { store, history } from "./store";
import Routes from "./routes";
// import { ConnectedRouter } from "connected-react-router";
import "./assets/css/main.css";

export class App extends Component {
  componentWillMount() {
    setTimeout(() => {
      ReactDOM.render(<LoaderGeneral />, document.getElementById("app"));
    }, 1);
  }

  componentDidMount() {
    setTimeout(() => {
      ReactDOM.render(
        // <Provider store={store}>
          <ThemeProvider theme={createTheme()}>
            <SoundsProvider sounds={createSounds(soundSettings)}>
              {/* <ConnectedRouter history={history}> */}
                <Routes />
              {/* </ConnectedRouter> */}
            </SoundsProvider>
          </ThemeProvider>,
        // </Provider>,
        document.getElementById("app")
      );
    }, 1500);
  }
  render() {
    return <div />;
  }
}

export default App;
